import tuiModal from "@/components/modal/modal"
export default {
	components: {
		tuiModal
	},
	data() {
		return {
			gid: '',
			pageid: 1,
			status: '',
			headerPosition: "fixed",
			headerTop: "0px",
			orderType: [{
					name: '全部',
					sort: 's',
				}, {
					name: '待付款',
					sort: '0'
				}, {
					name: '待发货',
					sort: '1'
				},
				{
					name: '配货中',
					sort: '2'
				}, 
				{
					name: '待收货',
					sort: '3'
				},
				{
					name: '已收货',
					sort: '4'
				},
				// {
				// 	name: '已作废',
				// 	sort: '5'
				// }, {
				// 	name: '已删除',
				// 	sort: '6'
				// }, 
				{
					name: '已退款',
					sort: '8'
				}
			],
			//订单列表 演示数据
			orderList: [],
			list: [],
			tabbarIndex: 0,
			scrollLeft: 0, //tab标题的滚动条位置
			txt: '', //退款原因
			// 退款弹框
			txtmodal: false,
			refundList: [],
			loadingText: '正在加载中...',
			top: 0,
			listscrollLeft: 0 // 向左滑动距离
		};
	},
	onLoad(option) {
		this.tabbarIndex = option.tbIndex;
		this.getlist();
		//兼容H5下排序栏位置









	},
	onShow() {

	},
	onPageScroll(e) {
		this.top = e.scrollTop;
		return;
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
	},
	methods: {
		scroll(e) {
			this.listscrollLeft = e.detail.scrollLeft;
		},
		// 获取订单
		getlist() {
			let data = {
				pageid: this.pageid,
				status: this.tabbarIndex != "s" ? this.tabbarIndex : '',
			};

			this.$xm.post('/Order/getList', data, res => {
				if (res.orderlist.length > 0) {
					res.orderlist.forEach(ele => {
						this.$xm.requestImg(ele.pro);
						ele.amtprice=parseFloat(ele.actually_amt)+parseFloat(ele.order_ship_price)
						switch (ele.order_state) {
							case '0':
								ele.tips = '未付款'
								break;
							case '1':
								ele.tips = '已付款'
								break;
							case '2':
								ele.tips = '配货中'
								break;
							case '3':
								ele.tips = '已发货'
								break;
							case '4':
								ele.tips = '已收货'
								break;
							case '5':
								ele.tips = '已作废'
								break;
							case '6':
								ele.tips = '已删除'
								break;
							case '7':
								ele.tips = '待退款'
								break;
							case '8':
								ele.tips = '已退款'
								break;
							default:

						}
					})
				}
				this.list = res.orderlist;
				this.loadingText = '到底了'
			})
		},
		// 切换导航 
		showType(tbIndex) {
			if (!tbIndex) {
				tbIndex = "s"
			}
			this.tabbarIndex = tbIndex;
			this.getlist();
		},
		// 跳转详情页
		toOrderDtl(e) {
			uni.navigateTo({
				url: './orderDtl/orderDtl?id=' + e
			})
		},
		toConfirm(e) {
			let data = {
				order_sn: e
			};
			this.$xm.post('/Order/received', data, (res) => {
				uni.showToast({
					title: res.msg,
					icon: 'none'
				})
				if(res.code==2000){
					this.getlist()
				}
			})
		},
		showLogistics(row) {
			uni.navigateTo({
				url: '../../logisticsTrack/track?id=' + row.id
			})
		},
		//再次购买
		buyAgain(row) {
			let store;
			if (uni.getStorageSync('store').store_code) {
				store = uni.getStorageSync('store').store_code;
			} else {
				store = 10001
			}
			uni.navigateTo({
				url: '../../order/confirmation?store=' + store + '&ordersn=' + row.order_sn
			})
			// let data = {
			// 	ordersn: row.order_sn
			// };				
			// this.$xm.post('/Order/buyAgain', data, res => {
			// 	if (res.s == 1) {
			// 		let store;
			// 		if(uni.getStorageSync('store').store_code){
			// 			store=uni.getStorageSync('store').store_code;
			// 		}else{
			// 			store=10001
			// 		}
			// 		uni.navigateTo({
			// 			url: '../../order/confirmation?store='+store+'&ordersn='+row.order_sn
			// 		})
			// 	}
			// })
		},

		// 修改地址
		edit(row) {
			console.log(row);
			uni.navigateTo({
				url: './orderEdit/orderEdit?sn=' + row.order_sn
			})
		},
		modalShow(row) {
			this.txtmodal = true;
			this.refundList = row;
		},
		// 退款
		refund() {
			console.log(this.refundList);
			this.txtmodal = false;
			let that = this;
			uni.showModal({
				title: '确定要申请退款吗',
				content: '申请后不可恢复',
				success: (res) => {
					if (res.confirm) {
						let params = {
							status: 7,
							ordersn: this.refundList.order_sn,
							memo: this.txt
						}
						this.$xm.post('/Order/changeStatus', params, eve => {
							if (eve.s == 1) {
								uni.showToast({
									title: '申请成功'
								})
								setTimeout(function() {
									that.getlist()
								}, 500)
							} else {
								uni.showToast({
									title: '申请失败'
								})
							}
						})
					} else if (res.cancel) {
						uni.showToast({
							title: '申请失败'
						})
					}
				}
			});


		},
		del(row) {
			let data = {
				ordersn: row.order_sn,
				status: '6'
			}
			this.$xm.post('/Order/changeStatus', data, (res) => {
				console.log(res);
				if (res.s == 1) {
					this.getlist();
					uni.showToast({
						title: '删除成功'
					})
				} else {
					uni.showToast({
						title: '不能重复删除！',
						icon: 'none'
					})
				}
			})
		},
		remindDeliver(row) {
			uni.showToast({
				title: '已提醒商家发货'
			})
		},
		cancelOrder(row) {
			uni.showModal({
				title: '取消订单',
				content: '确定取消此订单？',
				success: (res) => {
					if (res.confirm) {
						console.log('用户点击确定');
						this.doCancelOrder(row.ordersn);
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		doCancelOrder(ordersn) {
			let typeNum = this.orderList.length;
			for (let i = 0; i < typeNum; i++) {
				let list = this.orderList[i];
				let orderNum = list.length;
				if (orderNum > 0 && list[0].type == 'unpaid') {
					for (let j = 0; j < orderNum; j++) {
						if (this.orderList[i][j].ordersn == ordersn) {
							this.orderList[i][j].type = 'cancelled';
							break;
						}
					}
				}

			}
		},
		// 付款
		toPayment(row) {
			let params = {
				order_sn: row.order_sn
			}
			this.$xm.post('/Order/rePay', params, res => {
				if (res.s == 1) {
					let data = res.data
					uni.requestPayment({
						timeStamp: data.timeStamp,
						nonceStr: data.nonceStr,
						package: data.package,
						signType: data.signType,
						paySign: data.paySign,
						success: function(res) {
							console.log(res)
						},
						fail: function(res) {
							console.log(res)
						},
						complete: function(res) {
							if (res.errMsg == 'requestPayment:ok') {
								uni.showModal({
									title: '支付成功！',
									content: '',
									showCancel: false,
									confirmText: '确  定',
									confirmColor: '#2c2c2c',
									success: function(res) {
										that.getlist()
									}
								})
							} else {
								uni.showModal({
									title: '支付失败！',
									content: '',
									showCancel: false,
									confirmText: '确  定',
									confirmColor: '#2c2c2c',
									success: function(res) {
										if (res.confirm) {

										}
									}
								})
							}

						}
					});
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			})

		},
		toPage() {
			uni.navigateTo({
				url: './orderDtl/orderDtl'
			})
		},
		toGroup(row) {
			uni.navigateTo({
				url: '../../seckill/join/join?sn=' + row.order_sn + '&groupid=' + row.group_id
			})
		}
	}
}
